.footer {
  background-color: $dark-blue;
}

.footer__wrapper {
    display: flex;
    padding-left: 45px;
    padding-right: 45px;
    padding-bottom: 32px;
    padding-top: 42px;
    max-width: 1250px;
    margin: 0 auto;

    @media (max-width: $vp-1199) {
        padding-top: 38px;
    }

  @media (max-width: $vp-767) {
    padding-left: 15px;
    padding-right: 15px;
    display: grid;
    padding-bottom: 45px;
}
}

.footer__logo {
    margin-right: auto;
    padding-top: 9px;

    @media (max-width: $vp-767) {
        grid-column: 1/2;
        grid-row: 1/2;
        padding-top: 12px;
    }
}

.footer__logo img {
    @media (max-width: $vp-1199) {
        width: 82px;
        height: 18px;
    }

    @media (max-width: $vp-767) {
        width: 68px;
        height: 15px;
    }
}

.footer__socials {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 11px;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-left: auto;

  @media (max-width: $vp-767) {
    grid-column: 1/-1;
    grid-row: 2/3;
    margin-left: 0;
    width: fit-content;
  }
}

.footer__socials li {
    @media (max-width: $vp-767) {
    width: min-content;
    }
}

.footer__list {
  display: grid;
  list-style: none;
  gap: 40px;
  margin: 0;
  padding: 0;
  grid-template-columns: auto auto auto auto auto;
  margin-left: 9px;

  @media (max-width: $vp-1199) {
    margin-left: 16px;
    gap: 30px;
  }

  @media (max-width: $vp-767) {
    display: grid;
    grid-column: 1/2;
    grid-row: 1/2;
    grid-template-columns: repeat(2, 1fr);
    // grid-auto-flow: column;
    grid-auto-rows: 1fr; 
    margin-left: 0;
    margin-bottom: 36px;
    padding-top: 10px;
    row-gap: 21px;
  }
}

.footer__list a {
  color: $color-default-white;
  font-family: $roboto;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;

  @media (max-width: $vp-767) {
    font-size: 16px;
    line-height: 19px;
}
}

.footer__list li {
    width: min-content;

    @media (max-width: $vp-1199) {
        display: flex;
        align-items: center;
        padding-top: 7px;
    }

    @media (max-width: $vp-767) {
        padding-top: 0;
    }
}

.footer__list li:nth-child(n+1) {
    @media (max-width: $vp-767) {
        grid-row: 2/3;
        grid-column: 1/2;
    }
}

.footer__list li:nth-child(n+2) {
    @media (max-width: $vp-767) {
        grid-row: 3/4;
        grid-column: 1/2;
    }
}
.footer__list li:nth-child(n+3) {
    @media (max-width: $vp-767) {
        grid-row: 1/2;
        grid-column: 2/3;
    }
}

.footer__list li:nth-child(n+4) {
    @media (max-width: $vp-767) {
        grid-row: 2/3;
        grid-column: 2/3;
    }
}


.footer__list li:nth-child(n+5) {
    @media (max-width: $vp-767) {
        grid-row: 3/4;
        grid-column: 2/3;
    }
}


.footer__list li:nth-child(n+6) {
    @media (max-width: $vp-767) {
        grid-row: auto;
        grid-column: 2/3;
    }
}